import dayjs, { Dayjs } from 'dayjs';

// ----------------------------------------------------------------------

export function formatToKoreanAmount(amt: string | number) {
  const hundredMillion = 100000000;
  const tenThousand = 10000;
  const thousand = 1000;
  const hundred = 100;
  const ten = 10;

  const transformedValue = typeof amt === 'string' ? Number(amt.replace(/,/g, '')) : amt;

  const num1 = Math.floor(transformedValue / hundredMillion);
  const num2 = Math.floor((transformedValue % hundredMillion) / tenThousand);
  const num3 = Math.floor((transformedValue % tenThousand) / thousand);
  const num4 = Math.floor((transformedValue % thousand) / hundred);
  const num5 = Math.floor((transformedValue % hundred) / ten);
  const num6 = Math.floor(transformedValue % ten);

  if (num1 + num2 + num3 + num4 + num5 + num6 === 0) return '';

  return (
    (num1 ? num1 + '억 ' : '') +
    (num2 ? num2 + '만 ' : '') +
    (num3 ? num3 + '천 ' : '') +
    (num4 ? num4 + '백 ' : '') +
    (num5 ? num5 + '십 ' : '') +
    (num6 ? num6 : '') +
    '원'
  );
}

export function getAge(date: string | Date | Dayjs) {
  const today = dayjs();
  const birthDate = dayjs(date);

  const age = today.year() - birthDate.year();
  const month = today.month() - birthDate.month();
  if (month < 0 || (month === 0 && today.date() < birthDate.date())) {
    return age - 1;
  } else {
    return age;
  }
}

export const dateFormatter = (value: string): string => {
  if (value === '') {
    return '';
  }

  const year = value.substring(0, 4);
  const month = value.substring(4, 6);
  const day = value.substring(6, 8);

  let formattedDate = year;
  if (month) {
    formattedDate += `-${month}`;
  }
  if (day) {
    formattedDate += `-${day}`;
  }

  return formattedDate;
};

export const dateToTodayFormatter = (value: string): string => {
  if (value === '') {
    return '';
  }

  const year = value.substring(0, 4);
  let month = value.substring(4, 6);
  let day = value.substring(6, 8);

  if (parseInt(month.substring(0, 1)) > 1) {
    month = `0${month}`;
  } else if (parseInt(month) === 0 || parseInt(month) > 12) {
    month = month.substring(0, 1);
  }

  if (parseInt(day.substring(0, 1)) > 3) {
    day = `0${day}`;
  } else if (parseInt(day) === 0 || parseInt(day) > 31) {
    day = day.substring(0, 1);
  }

  const currentYear = new Date().getFullYear().toString();

  if (parseInt(year.substring(0, 1)) > parseInt(currentYear.substring(0, 1))) {
    return '';
  } else if (parseInt(year.substring(0, 2)) > parseInt(currentYear.substring(0, 2))) {
    return year.substring(0, 1);
  } else if (parseInt(year.substring(0, 3)) > parseInt(currentYear.substring(0, 3))) {
    return year.substring(0, 2);
  } else if (parseInt(year) > parseInt(currentYear)) {
    return year.substring(0, 3);
  }

  let formattedDate = year;
  if (month) {
    formattedDate += `-${month}`;
  }
  if (day) {
    formattedDate += `-${day}`;
  }

  return formattedDate;
};

export const telFormatter = (value: string): string => {
  if (value === '') {
    return '';
  }

  const firstValue = value.substring(0, 3);
  const secondValue = value.substring(3, 7);
  const lastValue = value.substring(7, 11);

  let formattedTel = firstValue;
  if (secondValue) {
    formattedTel += `-${secondValue}`;
  }
  if (lastValue) {
    formattedTel += `-${lastValue}`;
  }

  return formattedTel;
};

export const currencyFormatter = (value: string | number) => {
  return value ? Intl.NumberFormat('ko-KR').format(Number(value)) : '';
};
